:root {
  --background: rgb(86, 127, 179, 0.9);
}
* {
  margin: 0;
  font-family: "Quicksand";
  color: #333;
}

header {
  background: rgb(86, 127, 179, 0.9);
  background: var(--background);
  text-align: center;
  position: fixed;
  z-index: 999;
  width: 100%;
}

.nav-toggle {
  display: none;
}
.nav-toggle-label {
  position: absolute;
  top: 0;
  left: 0;
  margin-left: 1em;
  height: 100%;
  display: flex;
  align-items: center;
}

.nav-toggle-label span,
.nav-toggle-label span::before,
.nav-toggle-label span::after {
  display: block;
  background: white;
  height: 2px;
  width: 2rem;
  border-radius: 2px;
  position: relative;
}

.nav-toggle-label span::before,
.nav-toggle-label span::after {
  content: "";
  position: absolute;
}

.nav-toggle-label span::before {
  bottom: 7px;
}

.nav-toggle-label span::after {
  top: 7px;
}

.mainnavbar {
  position: absolute;
  text-align: left;
  top: 100%;
  left: 0;
  background: rgb(86, 127, 179, 0.9);
  background: var(--background);
  width: 100%;
  transform: scale(1, 0);
  transform-origin: top;
  transition: transform 400ms ease-in-out;
}

.mainnavbar .navul {
  margin: 0;
  padding: 0;
  list-style: none;
}

.mainnavbar .navli {
  margin-bottom: 1em;
  margin-left: 1em;
}

.mainnavbar .navlink {
  color: white;
  text-decoration: none;
  font-size: 1.2rem;
  text-transform: uppercase;
  opacity: 0;
  transition: opacity 150ms ease-in-out;
}
nav a :hover {
  color: black;
}

/* lookng for nav sibbling toggle */
.nav-toggle:checked ~ nav {
  transform: scale(1, 1);
}

.nav-toggle:checked ~ nav a {
  opacity: 1;
  transition: opacity 250ms ease-in-out 250ms;
}

.cta {
  font: inherit;
  font-weight: 500;
  border: 1px solid rgba(54, 162, 235, 1);
  background: rgba(54, 162, 235, 0.2);
  color: rgb(50, 4, 255);
  padding: 0.25rem 1rem;
  cursor: pointer;
  border-radius: 5px;
  transition: color 250ms ease-in, background 250ms ease-in;
  min-width: 8rem;
  align-items: right;
}

.cta:hover,
.button:active {
  background: rgba(54, 162, 235, 0.5);
  color: black;
}

.fancy_link {
  font-size: 2rem;
  font-weight: 400;
  text-decoration: none;
  text-transform: capitalize;
  color: rgb(37, 53, 104) !important;
  position: relative;
}

.fancy_link::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 2px;
  background: currentColor;

  transform: scaleX(0);
  transform-origin: right;
  transition: transform 250ms ease-in;
}

.fancy_link:hover::after {
  transform: scaleX(1);
  transform-origin: left;
}

.alertBar {
  display: -ms-grid;
  display: grid;
  margin: auto;
  align-items: center;
  text-align: center;
  max-width: 600px;
  margin: 0 auto;
  border-bottom: 1px solid #f2f2f2;
