@media screen and (min-width: 50rem) {
  .nav-toggle-label {
    display: none;
  }

  header {
    display: grid;
    grid-template-columns: auto minmax(20rem, 2fr) 1fr;
  }

  .logo {
    grid-column: 1 / 2;
  }

  .mainnavbar {
    /* all: unset; */
    position: relative;
    text-align: left;
    transition: none;
    transform: scale(1, 1);
    background: none;
    top: auto;
    top: initial;
    left: auto;
    left: initial;

    grid-column: 2/3;
    display: flex;
    justify-content: flex-end;
    align-items: center;
  }

  .mainnavbar .navlink {
    opacity: 1;
    position: relative;
  }

  .mainnavbar .navlink::before {
    content: "";
    display: block;
    height: 5px;
    background: rgb(37, 53, 104);
    position: absolute;
    top: -0.75em;
    left: 0;
    right: 0;
    transform: scale(0, 1);
    transition: transform ease-in-out 250ms;
  }

  .mainnavbar .navlink:hover::before {
    transform: scale(1, 1);
  }

  .mainnavbar .navul {
    display: flex;
  }
  .mainnavbar .navli {
    margin-left: 3em;
    margin-bottom: 0;
  }
  .fancy_link {
    font-size: 2rem;
    font-weight: 400;
    text-decoration: none;
    text-transform: capitalize;
    color: rgb(37, 53, 104) !important;
    position: relative;
    white-space: nowrap;
  }
}

@media screen and (max-width: 27rem) {
  .fancy_link {
    font-size: 1.5rem;
    font-weight: 400;
    white-space: nowrap;
  }
}

